import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbarr from 'react-bootstrap/Navbar';
import '../index.css';
import logo from '../Asserts/logo.png';

export const Navbar = () => {
  return (
  <>
  <nav>
  <Navbarr bg="dark" variant="dark" >
      <Container>
        <Navbarr.Brand href="#home"><img width='40px' height='40px' src={logo} alt="logo"/> SIMMI FOUNDATION</Navbarr.Brand>
        <Navbarr.Toggle aria-controls="basic-navbar-nav" />
        <Navbarr.Collapse id="basic-navbar-nav">
          <Nav className="nav">
            <Nav.Link href="#home">Home</Nav.Link>
            <Nav.Link href="#about">About</Nav.Link>
            <Nav.Link href="#donate">Donate</Nav.Link>
            <Nav.Link href="#blog">Blog</Nav.Link>
            <Nav.Link href="#gallery">Gallery</Nav.Link>
            <Nav.Link href="#volunteer">Volunteer</Nav.Link>
            <Nav.Link href="#events">Events</Nav.Link>
            <Nav.Link href="#career">Career</Nav.Link>
            </Nav>
        </Navbarr.Collapse>
      </Container>
    </Navbarr>
    <marquee scrollamount="12" width="100%" style={{ color: 'black', backgroundColor: 'white', fontSize: '1em' }}>  Smart India Multi Management Institute!    स्मार्ट इंडिया बहुद्देश्यीय प्रबन्धन संस्थान!   Registration number : 085953/2020    Niti Aayog (Govt. Of India ) Unique Id : HR/2020/0258148</marquee>
    </nav>
  </>
    
  )
}
