import '../index.css';
import arrow from '../Asserts/arrow.png';

export const Help = (props) => {
  return (
    <>
    <div className='helpcard'>
        <div className='title'><h5>{props.title}</h5></div>
        <hr></hr>
        <div className='body'>{props.body}</div>
        <div className='more'>{props.more} <img width='27px' height='27px' src={arrow} alt="arrow"/></div>
    </div>
    </>
  );
}
