import '../index.css';
import arrow from '../Asserts/arrow.png';
import Map from '../Asserts/place.png';
import Clock from '../Asserts/clock.png';
import Date from '../Asserts/date.png';

export const Upcoming = (props) => {
  return (
    <>
    <div className='upcomingcard'>
        <div className='img'><img src={props.img}  alt=''/></div>
        <div className='title'><h5>{props.title}</h5></div>
        <div className='place'><img width='20px' height='20px' src={Map} alt="map"/>{props.place}</div>
        <div className='date'><img width='20px' height='20px' src={Clock} alt="clock"/>{props.time} <img width='20px' height='20px' src={Date} alt="date"/>{props.date}</div>
        <div className='body'>{props.body}</div>
        <div className='more'>Read more <img width='20px' height='20px' src={arrow} alt="arrow"/></div>
    </div>
    </>
  );
}
