import React, { Component} from 'react';
import '../index.css';

import map from '../Asserts/map.png';
import obj1 from '../Asserts/obj1.jpg';
import obj2 from '../Asserts/obj2.jpg';
import obj3 from '../Asserts/obj3.jpg';
import obj4 from '../Asserts/obj4.jpg';


import {Navbar} from './Navbar';
import { Slider } from './Slider';
import { Objective } from './Objective';
import { Help } from './Help';
import { Events } from './Events';
import { Footer } from './Footer';


export default class Landing extends Component {
  
  render() {
    return (
      <>
      <Navbar/>
      <Slider/>

      <section className='about'>
        <div className="row">
          <div className="column">
            <h3 className='body-heading'>Welcome to <b style={{color : '#f58634'}}>SIMMI </b> 
            </h3>
            <p className='body-text'>
              Smart India Multi Management Institute is a pan India NGO registered in Haryana, India; carrying out welfare projects on
              education, healthcare, livelihood and women empowerment. We believe that unless members of
              the civil society are involved proactively in the process of development, sustainable change will not
              happen. Based on this, Simmi Foundation sensitizes and engages the civil society, making it an active
              partner in all its welfare initiatives.
            </p>
          </div>
          <div className="column">
            <img src={map} height={450} width={350} alt="dark-map" />
          </div>
        </div>
      </section>

      <section className='objectives'>
        <h3 className='body-heading'>Objectives</h3> 
        <div className='row'>
          <div className='column'><Objective img={obj1} title={"Education"} body={"We provide free academic education, scholarship, training and other incentives to the children."}/></div>
          <div className='column'><Objective img={obj2} title={"Livelihood"} body={"We implement various schemes to provide livelihood and uplift the poor in society."}/></div>
          <div className='column'><Objective img={obj3} title={"Healthcare"} body={"We are increasing awareness on Sanitization and providing people with access to better nutrition, clean water and toilets."}/></div>
          <div className='column'><Objective img={obj4} title={"Women and Youth Empowerment"} body={"We focus on providing equal opportunities for women in the work field."}/></div>
        </div>
      </section>

      <section className='help'>
        <h3 className='body-heading'>How can you help us</h3>
        <div className='row'>
          <div className='column'><Help title={"Collaborate"} body={"Simmi Foundation serves in the collaboration with schools, colleges and other institutions."} more={"Collaborate with us"}/></div>
          <div className='column'><Help title={"Donate Money"} body={"Your donation will further our mission of ensuring a happy and healthy life of those in need."} more={"Donate now"}/></div>
          <div className='column'><Help title={"Be a Volunteer"} body={"Even the all-powerful Pointing has no control about the blind texts."} more={"Read more"} /></div>
        </div>
      </section>

      <Events />
      <Footer />
      
      </>
    )
  }
}
