import { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import slider1 from '../Asserts/slider1.jpg';
import slider2 from '../Asserts/slider2.jpg';
import slider3 from '../Asserts/slider3.jpg';
import slider4 from '../Asserts/slider4.jpeg';

export const Slider = () =>  {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
  return (
    <Carousel activeIndex={index} onSelect={handleSelect} > 
          <Carousel.Item>
            <img className="d-block w-100" height={650} src={slider1} alt="First slide" />
          </Carousel.Item>
          <Carousel.Item>
            <img className="d-block w-100" height={650} src={slider2} alt="Second slide" />
          </Carousel.Item>
          <Carousel.Item>
            <img className="d-block w-100" height={650} src={slider3} alt="Third slide" />
          </Carousel.Item>
          <Carousel.Item>
            <img className="d-block w-100" height={650} src={slider4} alt="fourth slide" />
          </Carousel.Item>
    </Carousel>

  );

}
